import React,{Fragment, useState} from 'react';

const EditTaxi = ({taxis}) => {

    const [name, setName] = useState(taxis.name);

    const updateName= async(e) =>{
        e.preventDefault();
        try {



            const body = {name};
            const response= await fetch(`/taxis/${taxis.id}`, {
                method: "PUT",
                headers: {"Content-Type": "application/json"},
                body: JSON.stringify(body)
            });
            window.location="/";
        } catch (err) {
            console.error(err.message);            
        }
    }     
    return (
        <Fragment>
            <button type="button" className="btn btn-warning" data-bs-toggle="modal" data-bs-target={`#id${taxis.id}`}>Edit</button>

                <div id={`id${taxis.id}`} class="modal fade">
                <div className="modal-dialog">

                
                    <div className="modal-content">
                    <div className="modal-header">
                        <button type="button" class="close" data-bs-dismiss="modal" onClick={()=> setName(taxis.name)}>&times;</button>
                        <h4 class="modal-title">Edit Taxi name</h4>
                    </div>
                    <div className="modal-body">
                        <input type="text" class="form-control" value={name} onChange={e=> setName(e.target.value)}/>
                    </div>
                    <div class="modal-footer">
                        <button type="button" className="btn btn-warning" data-bs-dismiss="modal" onClick={e=> updateName(e)}>Edit</button>
                        <button type="button" className="btn btn-default" data-bs-dismiss="modal" onClick={()=> setName(taxis.name)}>Close</button>
                    </div>
                    </div>

                </div>
            </div>


        </Fragment>
    );
};

export default EditTaxi;